.input-field {
    display: -ms-flexbox;
    display: flex;
    padding: 0.75rem;
    background-color: #fff;
}

.input-field__textarea {
    -ms-flex-positive: 1;
        flex-grow: 1;
    margin-right: 1rem;
    border: solid 1px var(--color-gray-light);
    border-radius: var(--border-radius);
    outline: none;
    resize: none;
    font-size: 1rem;
    color: var(--color-gray-dark);
}
.input-field__textarea::-webkit-input-placeholder {
    color: var(--color-gray-light);
    font-weight: 300;
}
.input-field__textarea::-moz-placeholder {
    color: var(--color-gray-light);
    font-weight: 300;
}
.input-field__textarea:-ms-input-placeholder {
    color: var(--color-gray-light);
    font-weight: 300;
}
.input-field__textarea::placeholder {
    color: var(--color-gray-light);
    font-weight: 300;
}

.input-field__textarea:hover,
.input-field__textarea:focus {
    border-color: var(--color-teal);
}

.input-field__button {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    padding: 0.5rem 2rem;
    border-radius: var(--border-radius);
    outline: none;
    background-color: var(--color-teal);
    text-transform: uppercase;
    color: #fff;
    -webkit-transition: background-color var(--animation-speed);
    -o-transition: background-color var(--animation-speed);
    transition: background-color var(--animation-speed);
}

.input-field__button:hover,
.input-field__button:active {
    background-color: var(--color-teal-dark);
}


