.login-gate {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    background-color: #fff;
}

.login-gate__text {

}

.login-gate__input {
    border: solid 1px var(--color-gray-light);
    border-radius: var(--border-radius);
    outline: none;
    resize: none;
    font-size: 1rem;
    color: var(--color-gray-dark);
}

.login-gate__input::-webkit-input-placeholder {
    color: var(--color-gray-light);
    font-weight: 300;
}

.login-gate__input::-moz-placeholder {
    color: var(--color-gray-light);
    font-weight: 300;
}

.login-gate__input:-ms-input-placeholder {
    color: var(--color-gray-light);
    font-weight: 300;
}

.login-gate__input::placeholder {
    color: var(--color-gray-light);
    font-weight: 300;
}

.login-gate__input:hover,
.login-gate__input:focus {
    border-color: var(--color-teal);
}



